.source-sans-pro {
  font-family: "Source Sans Pro", sans-serif;
}

.merriweather {
  font-family: "Merriweather", sans-serif;
}

.open-sans {
  font-family: "Open Sans", sans-serif;
}

.montserrat {
  font-family: "Montserrat", sans-serif;
}

.lead-paragraph {
  font-family: "Merriweather", sans-serif;
  font-size: 20px;
  line-height: 34px;
  color: #205493;
}

.sfgov-logo {
  display: inline-flex;
  flex-direction: row;
  flex-wrap: no-wrap;
  align-items: stretch;
}

.sfgov-logo .sfgov-logo__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 14px;
}

.sfgov-logo .sfgov-logo__text .sfgov-logo__text--pre-title {
  font-family: "Helvetica", sans-serif;
  font-size: 10px;
  line-height: 12px;
  color: #212121;
}

.sfgov-logo .sfgov-logo__text .sfgov-logo__text--title {
  font-family: "Merriweather", sans-serif;
  font-size: 20px;
  font-weight: bold;
  line-height: 26px;
}

.sfgov-logo .sfgov-logo__text .sfgov-logo__text--title a {
  text-decoration: none;
  color: #212121;
}
